* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

:root {
  --bg: hsl(215, 43%, 11%);
  --bg-content: hsl(215, 42%, 21%);
  --details: hsl(40, 100%, 57%);
}

body {
  font-family: sans-serif;
  background-color: var(--bg);
  padding: 2rem;
}
/*768 480*/
@media (max-width: 768px) {
  body {
    padding: 2rem;
  }
}
@media (max-width: 480px) {
  body {
    padding: 1.5rem;
  }
}
