.tasks {
  margin-top: 2rem;
}

.tasks li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6rem;
  transition: all 400ms;
}

.tasks li:hover {
  background-color: hsla(225, 5%, 29%, 0.36);
}

.tasks li .edit {
  margin-right: 0.7rem;
  color: #80eb78;
}

.tasks li .delete {
  margin-right: 0.7rem;
  color: var(--details);
}

.tasks li .edit,
.tasks li .delete {
  cursor: pointer;
}

.tasks li span {
  display: flex;
  align-items: center;
}
