.form {
  display: flex;
  align-items: center;
  justify-content: center;
}

input,
button {
  height: 1.8rem;
  border: none;
}

input {
  margin-right: 0.5rem;
  padding: 0.35rem;
  min-width: 20rem;
  font-size: 1rem;
  background-color: rgba(255, 255, 255, 0.411);
}

button {
  height: 1.8rem;
  border: none;
}

button {
  background-color: var(--bg-content);
  color: var(--details);
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

@media (max-width: 480px) {
  input {
    min-width: 5rem;
  }
}
