.main {
  background-color: hsl(215, 44%, 11.5%);
  max-width: 35rem;
  padding: 0.5rem 0rem;
  margin: auto;
}
.content {
  background-color: var(--bg-content);
  color: rgba(255, 255, 255, 0.842);
  padding: 2rem;
  margin: 3rem auto;
  max-width: 30rem;
  box-shadow: 15px 15px 0px 2px hsl(215, 57%, 9%);
  border-radius: 0.75rem;
}

.content h1 {
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 588px) {
  .main {
    padding: 0.3rem 0rem;
    background-color: var(--bg);
  }

  .content {
    padding: 2rem;
    box-shadow: 15px 15px 0px 0px hsl(215, 57%, 9%);
  }
}
@media (max-width: 480px) {
  .content h1 {
    font-size: 1.8rem;
  }
}
